@import "./../base/vars";
.solutions {
  margin: 0 auto;
  &__title {
    color: $accentColor;
    font-family: Lato-Black;
    font-size: 60px;
    margin-bottom: 70px;
    margin-top: 24px;
    @media all and (max-width: 1300px) {
      margin-bottom: 30px;
    }
  }
  &__item {
    font-family: Lato-Ligh;
    font-size: 24px;
    line-height: 190%;
    font-weight: 500;
    margin-left: 70px;
    position: relative;
    @media all and (max-width: 1300px) {
      font-size: 1.4rem;
    }
    @media all and (max-width: 1100px) {
      font-size: 1.3rem;
    }
    @media all and (max-width: 900px) {
      font-size: 1.2rem;
    }
    @media all and (max-width: 400px) {
      font-size: 1.1rem;
      line-height: 130%;
    }
  }
  &__item:nth-child(1)::before,
  &__item:nth-child(2)::before,
  &__item:nth-child(3)::before,
  &__item:nth-child(4)::before,
  &__item:nth-child(5)::before,
  &__item:nth-child(6)::before,
  &__item:nth-child(7)::before,
  &__item:nth-child(8)::before {
    content: "";
    position: absolute;
    width: 38px;
    height: 38px;
  }
  &__item:nth-child(1)::before {
    background: url(./../images/solutions/Vector1.png) no-repeat center;
    top: 4px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }
  &__item:nth-child(2)::before {
    background: url(./../images/solutions/Vector2.png) no-repeat center;
    top: 10px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }
  &__item:nth-child(3)::before {
    background: url(./../images/solutions/Vector3.png) no-repeat center;
    top: 10px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }
  &__item:nth-child(4)::before {
    background: url(./../images/solutions/Vector4.png) no-repeat center;
    top: 10px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }
  &__item:nth-child(5)::before {
    background: url(./../images/solutions/Vector5.png) no-repeat center;
    top: 10px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }
  &__item:nth-child(6)::before {
    background: url(./../images/solutions/Vector6.png) no-repeat center;
    top: 10px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }
  &__item:nth-child(7)::before {
    background: url(./../images/solutions/Vector7.png) no-repeat center;
    top: 10px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }
  &__item:nth-child(8)::before {
    background: url(./../images/solutions/Vector8.png) no-repeat center;
    top: 10px;
    left: -46px;
    @media all and (max-width: 640px) {
      top: 0;
    }
  }

  &__wrapper {
    padding-top: 70px;
    display: flex;
    justify-content: center;
    @media all and (max-width: 1200px) {
      align-items: flex-end;
    }
    @media all and (max-width: 760px) {
      flex-direction: column;
      padding-top: 30px;
    }
  }
  &__images {
    @media all and (max-width: 1300px) {
      margin-top: 30px;
    }
    @media all and (max-width: 941px) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
  &__wrapper {
    position: relative;
  }
  &__wrap::before {
    content: "";
    position: absolute;
    background: url(./../images/solutions/Vector0.png) no-repeat center;
    width: 56px;
    height: 35px;
    top: 35px;
    left: 416px;
    @media all and (max-width: 1300px) {
      top: 30px;
    }
    @media all and (max-width: 700px) {
      top: 5px;
      left: 230px;
    }
  }
  &__img {
    @media all and (max-width: 1300px) {
      max-width: 640px;
      width: 100%;
    }
    @media all and (max-width: 1200px) {
      max-width: 420px;
      width: 100%;
    }
  }
  @media all and (max-width: 760px) {
    max-width: 350px;
    width: 100%;
  }
}
