  /* border: 1px solid red; */
}

strong {
  font-weight: bold;
}

.container,
.main_container,
.footer_container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 100px 0;
  @media all and (max-width: 1248px) {
    padding: 0 70px 0;
  }
  @media all and (max-width: 1180px) {
    padding: 0 45px 0;
  }
  @media all and (max-width: 994px) {
    padding: 0 30px 0;
  }
  @media all and (max-width: 600px) {
    padding: 0 20px 0;
  }
  @media all and (max-width: 380px) {
    padding: 0 13px 0;
  }
}
.container {
  padding-top: 42px;
  @media all and (max-width: 1180px) {
    padding-top: 30px;
  }
}
.container {
  max-width: 100% !important;
  overflow-x: hidden !important;
}
@media screen and (max-width: 667px) {
  body {
    overflow-x: hidden !important;
  }

  .container,
  .main_container,
  .footer_container {
    max-width: 100% !important;
    overflow-x: hidden !important;
  }
}
